@font-face {
  src: local('Kooka-Regular'), url(/to-be-wed/static/media/Kooka-Regular.2599e0b5.ttf), format('ttf');
  font-family: 'Kooka-Regular';
}

html {
  background-color: black;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Kooka-Regular', Arial, sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  src: local('Kooka-Regular'), url(/to-be-wed/static/media/Kooka-Regular.2599e0b5.ttf), format('ttf');
  font-family: 'Kooka-Regular';
}

:root {
  /* root colors, these are all of the flowers in the canva logo i made */
  --dark-green: #5b846d;
  --orange: #ef7525;
  --lilac: #cb6ce6;
  --red: #ef1963;
  --purple: #8c52ff;
  --blue: #80bef8;
  --lime: #c9e265;
  --yellow: #ffde59;
}

.App {
  text-align: center;
  background-color: black;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@font-face {
  src: url(/to-be-wed/static/media/Kooka-Regular.2599e0b5.ttf);
  font-family: 'Kooka-Regular';
}

.navbar-light .navbar-toggler-icon {
  background-image: url(/to-be-wed/static/media/collapse.6b766028.svg) !important;
}

.nav-link {
  font-family: 'Kooka-Regular';
  color: white !important;
  font-size: 1.5rem;
  font-weight: bold;
  width: 100%;
}

.nav-link:nth-child(1) {
  color: var(--yellow) !important;
}

.nav-link:nth-child(2) {
  color: var(--purple) !important;
}

.nav-link:nth-child(3) {
  color: var(--orange) !important;
}

.nav-link:nth-child(4) {
  color: var(--dark-green) !important;
}

.nav-link:nth-child(5) {
  color: var(--blue) !important;
}

.nav-link:nth-child(6) {
  color: var(--blue) !important;
}

/* TABLET SIZE */
@media (min-width: 576px) {
  .nav-link {
  }
}
/* LARGE TABLET/DESKTOP SIZE */
@media (min-width: 992px) {
  .nav-link {
    font-size: 2rem;
  }
}

.carousel {
  border: 5px solid var(--yellow);
  border-radius: 10px;
  margin: 1.5rem;
}

.carousel-item > img {
  border-radius: 5px;
  padding: 0 !important;
}

.our-story {
  border: 5px solid var(--purple);
  border-radius: 10px;
  margin: 1.5rem;
}

.accordion {
  margin: 0 auto;
  margin-top: 30px;
  border: 5px solid var(--orange);
  border-radius: 10px;
  margin: 1.5rem;
}

.faq-title {
  color: var(--orange);
  font-size: 1.5rem !important;
  font-family: 'Kooka-Regular';
  font-weight: bold;
  padding: 1rem;
}

.accordion-button {
  border-radius: 20px;
  font-size: 0.9rem !important;
  background-color: var(--orange) !important;
  color: black !important;
  font-weight: bold;
}

.accordion-body {
  background-color: var(--yellow);
  color: black;
  font-size: 0.8rem;
  text-align: start;
}

li {
  list-style: none;
}

img {
  width: 95%;
  margin: 0 auto;
  padding: 2rem 0;
}

/* TABLET SIZE */
@media (min-width: 576px) {
  img {
    width: 80%;
    padding: 3rem 0;
  }
}
/* LARGE TABLET/DESKTOP SIZE */
@media (min-width: 992px) {
  img {
    width: 70%;
    padding: 4rem 0;
  }
}

.footer > img {
  width: 40%;
  padding-bottom: 1rem;
}

